
.toast-message {
  position: relative;
  margin-bottom: 10px;
  padding: 0 10px;
  background-color: white;
  border: 1px solid #e3e3e3;
  border-left: 5px solid #f58989;
  color: #adadad;
}
.toast-message .toast-header {
    color: #f9fafb;
    margin-top: 15px;
}
.toast-message .toast-header i {
      position: absolute;
      right: 0;
      top: 0;
      padding: 3px;
}
.toast-message .pipe {
    margin: 0 5px;
}
.toast-message.error {
    background-color: #f2dede;
    border-left-color: #ebccd1;
    color: #a94442;
}
.toast-message.error .toast-header {
      color: #a94442;
}
.toast-message.warning {
    background-color: #fcf8e3;
    border-left-color: #faebcc;
    color: #8a6d3b;
}
.toast-message.warning .toast-header {
      color: #8a6d3b;
}



